import axios from '../axios'
import {ARTICLE, COMMENT, USER} from '../classMap'
import {ACLWriteSetMe, listPage, updateDataCount, userId} from '../api-util'

export default {
    list({page = 1, articleId}) {
        return axios.get(`/classes/${COMMENT}`, {
            params: {
                where: {
                    articleId,
                    state: {
                        $ne: '0'
                    }
                },
                include: 'auth',
                ...listPage(page)
            }
        })
    },
    detail(objectId) {
        return axios.get(`/classes/${COMMENT}/${objectId}`, {
            params: {
                include: 'auth'
            }
        }).then(res => {
            return res.data
        })
    },
    create({content, articleId}) {
        const data = {
            articleId,
            content,
            auth: {
                __type: 'Pointer',
                className: USER,
                objectId: userId()
            },
            state: '1', // 0删除，1正常
            zanUser: [],
            counts: {
                zan: 0,
                comment: 0,
            },
            ACL: ACLWriteSetMe()
        }
        return axios.post(`/classes/${COMMENT}`, data).then(res => {
            updateDataCount(ARTICLE, articleId, 'counts.comment')
            return res
        })
    },
    remove(id, articleId) {
        return axios.put(`/classes/${COMMENT}/${id}`, {
            state: '0'
        }).then(res => {
            updateDataCount(ARTICLE, articleId, 'counts.comment', -1)
            return res
        })
    }
}