import utils from '../util'
import axios from './axios'

/**
 * 创建Pointer对象
 * @param {String} className
 * @param {String} objectId
 * @return Object
 * */
export function createPointer(className, objectId) {
    return {
        __type: 'Pointer',
        className,
        objectId
    }
}

/**
 * 仅自己和管理员可写，所有人可读
 * @return Object
 * */
export function ACLWriteSetMe() {
    const id = utils.store.get('userInfo').objectId
    return {
        '*': {
            read: true,
        },
        [id]: {
            write: true
        },
        'role:Admin': {
            write: true,
        }
    }
}

/**
 * 当前登录用户ID
 * @return String
 * */
export function userId() {
    try {
        return utils.store.get('userInfo').objectId || ''
    } catch (e) {
        return ''
    }
}

/**
 * 分页倒序查询列表
 * @return Object
 * */
export function listPage(page) {
    return {
        count: '1',
        order: '-createdAt',
        limit: (page - 1) + 10,
        skip: (page - 1) * 10
    }
}


/**
 * 数字增加/减少
 * @param {String} classes
 * @param {String}  id
 * @param {String} key
 * @param {Number} amount [1,-1]
 * @return Object
 * */
export async function updateDataCount(classes, id, key, amount = 1) {
    return axios.put(`/classes/${classes}/${id}`, {[key]: {__op: 'Increment', amount}})
}

